.paneWrapper {
	/*width: calc((100% / 5) - (30px));*/
	width: 178px;
	height: 278px;
	overflow: hidden;
	border-radius: 3px;
	margin: 10px 25px;
	-webkit-box-shadow: 4px 5px 5px 0px rgba(0,0,0,0.11);
	box-shadow: 4px 5px 5px 0px rgba(0,0,0,0.11);
	border: 1px solid rgba(0,0,0,0.31);
	display: inline-block;
}

.pane {
	width: 100%;
	height: 100%;
	background-size: cover;
}

.overlay {
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.65);
	opacity: 0;
	-webkit-transition: all 0.15s ease-in;
	-o-transition: all 0.15s ease-in;
	transition: all 0.15s ease-in;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.overlay:hover {
	opacity: 1;
	-webkit-transition: all 0.15s ease-in;
	-o-transition: all 0.15s ease-in;
	transition: all 0.15s ease-in;
	cursor: pointer;
}

.overlay p {
	color: white;
	font-size: 18px;
	text-align: center;
	margin-bottom: 5px;
}

.overlay p.title {
	font-weight: bold;
	padding: 0px 10px;
}

.overlay img {
	width: 24px;
	height: 24px;
}

.inputOverlay {
	position: absolute;
	background: black;
	width: 100%;
	height: 100%;
}

.paginateWrap {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.LazyLoad {
	display: inline-block;
}


