.movie .title {
	font-family: Open Sans Condensed;
	margin-top: 0px;
}

.movie {
	padding-top: 30px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	padding-left: 30px;
	height: calc(100vh - 51px);
}

.header {
	margin-left: 30px;
}

.movie p {
	font-size: 1.15em;
}

.top {
	width: 100%;
	height: 65vh;
	background-attachment: fixed;
    background-position-y: -40%;
    background-repeat: no-repeat;
    background-size: cover;
}

.is-visible {
	width: 100%;
}


